						<div class="sign-box-1 clearfix">
							<div class="fl flex-box">
								<div class="tips" style="width:20px;">注：</div>
								<ul class="flex-item1 tips">
									<li>（1）人员类别是指：研究人员、技术人员和博士后；</li>
									<li>（2）仅限填写编制内科研人员和博士后，包括项目首席专家和参与人员；</li>
									<li>（3）本表格按项目以每个参与单位为单位填写，人员岗位状态为初具本证明之日情况；</li>
									<li>（4）表格行数不够可自行添加，不得改变本表格结构和内容。</li>
