							</div>
						</div>
						<h5 class="sub-title"><span class="bar"></span>中国医学科学院医学与健康科技创新工程项目申请人员未承担2018年和2019年创新工程任务承诺</h5>

					</div>
				</el-form>
				<!-- 填写说明弹窗 -->
				<dt-dialog key="3" class="large-dialog" :hasContent="true" :isShow="dialogShow" :hasBtn="false" :title="firstStepShow?'填写说明':'共同条款'">
			        <div class="dialog-content">
			        	<template v-if="firstStepShow">
				        	<p style="margin-bottom: 30px">目标任务书必须依据通过综合评审的项目申报书和评审反馈意见如实填写，不得随意变更内容。</p>
				        	<h5>一、项目基本信息</h5>
							<ul>
								<li>1.项目名称应以中国医学科学院医学与健康科技创新工程（以下简称创新工程）批复的名称为准。</li>
								<li>2.牵头单位为项目组织实施的责任单位，目标任务书由牵头单位组织填写，编写人员应客观、真实地填报有关数据，尊重他人知识产权，遵守国家有关知识产权法规。</li>
								<li>3.对于伪造、篡改科学数据，抄袭他人著作、论文或者剽窃他人科研成果等科研不端行为，一经查实，将记入信用记录。</li>
								<li>4.系统默认正文小4号宋体、1.5倍行距，双面打印，简单装订，系统自动生成申报书将不改变表格格式和结构。</li>
								<li>5.项目须三家及以上所院联合申报，只能由一家科研院所牵头。</li>
								<li>6.每个项目只设一名首席专家。根据需要可以设一名共同首席专家。项目骨干人员原则上应为院校所属单位在编科研人员和博士后，研究生和编制外聘用人员一般不应列入。</li>
								<li>7.专业应填目前所从事领域的专业，按二级学科专业填写，参照国家自然科学基金委的专业分类目录。 </li>
							</ul>
							<h5 style="margin-top: 30px;">二、项目首席专家签字</h5>
							<p>项目首席专家应承诺研究的真实性、实验材料和知识产权共享，打印后在所有上报的目标任务书中亲笔签字。</p>
			        	</template>
			        	<template v-if="!firstStepShow">
			        		<p>（一）缔约各方的权利、义务</p>
			        		<p>第一条  缔约各方均应共同遵守《中华人民共和国合同法》、《中华人民共和国著作权法》、《中华人民共和国专利法》、《中华人民共和国科学技术进步法》及《中国医学科学院医学与健康科技创新工程项目管理办法》、《院外单位承担中国医学科学院医学与健康科技创新工程项目管理办法》、《中国医学科学院医学与健康科技创新工程项目经费管理办法》等法律和规定，严格遵守并认真履行本合同的各项条款，以及科研诚信的有关规定。</p>
			        		<p>（一）甲方</p>
		    				<p>甲方是中国医学科学院，应协调有关部门按合同约定提供研究经费。</p>
							<p>甲方有权监督、检查合同履行情况。合同履行期间，甲方根据需要有权委派专家组或科技评估、监理机构以及审计机构，对乙方和丙方履行本合同的情况进行检查、监督和审计。丙方完成项目研究任务后，由甲方负责进行项目验收。</p>
							<p>（二）乙方</p>
							<p>乙方是项目牵头单位，应按合同约定，为项目实施提供条件支撑和管理服务，对实施项目予以监督。</p>
							<p>乙方在签订本合同时，保证乙方具备承担项目资格所需的各项条件；如果乙方提供的相关信息不真实，并足以影响乙方承担项目资格的，本合同将视为无效合同，丙方承担由此产生的一切损失。</p>
							<p>（三）丙方</p>
							<p>丙方是首席专家，负责实施项目的研究和开发，具有选聘研究骨干和技术人员，选择科研路径与经费调控分配等权利。丙方有权利制定任务负责人，并授权任务负责人编制任务研究经费预算、决算，严格执行批准的预算。</p>
							<p>乙方及丙方与协作单位签订的与本项目相关的协议或合同必须符合有关法律法规和本合同条款的规定，甲方和审计部门有权进行监督。乙方及丙方应及时向甲方、审计部门提供真实准确的信息。并积极配合或参加由甲方召集的有关本项目的监督、管理和评估等活动。</p>
							<p>乙方及丙方在项目实施过程中应采取相应措施，避免发生危及国际关系、造成不良政治影响、妨害经济运行等损害国家利益的活动。当可能出现危及国家利益及社会公共利益、影响项目完成或其它可能违反合同条款的事件时，乙方及丙方应及时告知甲方，并承担由此产生的一切责任和损失。</p>
							<p>丙方开展的一切与项目有关的活动如涉及伦理问题、生物安全问题、实验动物福利、人类遗传资源管理等问题，应确保有关研究人员遵守相关法律法规。 乙方及丙方应承担维护实验环境卫生、安全的责任，做好安全防护措施，如因执行本项目而导致人员生命、健康、财产等受到侵害或使环境受到损害，乙方及丙方应负完全责任，并承担由此产生的一切损失。</p>
							<p>（三）其他</p>
							<p>丙方应严格履行合同义务，按合同要求完成项目研究任务。在科研活动中，严格遵守科研诚信有关规定，不弄虚作假。</p>
							<p>丙方应在每年1月31日前向甲方及乙方提交中国医学科学院医学与健康科技创新工程项目年度执行情况报告，特殊情况参照《中国医学科学院医学与健康科技创新工程项目管理办法》处理。</p>
							<p>第二条  甲方有权根据丙方项目计划进度完成情况决定是否拨付（或减拨）后续经费和终止项目。</p>
							<p>丙方使用经费应严格按照项目经费预算和合同约定的支出范围执行，保证专款专用，不得弄虚作假、挪用、挤占项目经费或违反相关法律法规。项目完成后，丙方应向甲方及乙方提交项目经费使用情况报告和使用项目经费购置仪器、设备、器材、固定资产等的明细帐目，并加盖财务印章，在项目验收时一并验收。</p>
							<p>第三条  项目结束后，项目研究过程中购置或试制形成的固定资产，一般由乙方及丙方负责管理和使用（合同另有约定的除外），其维护运转费用由乙方承担。</p>
			        	</template>
			        </div>
